import axios from 'axios';
import React, { useState,useEffect } from 'react';
import { Link, useNavigate, useParams } from 'react-router-dom';

export default function
    () {

    const { id } = useParams();
    let navigate = useNavigate();

        const [loanapplication, setLoanapplication] = useState({
        userId: "",
        id:"",
        loanAmount: "",
        expectedTenure:"",
        annualIncome: "",
        loanUsedFor: "",
        maritalStatus: "",
        address: "",
        city: "",
        state: "",
        userType: "",
        status:"Pending"
    });

    useEffect(() => {
        loadApplications();
    }, []);

    const handleToClose =async () => {
        console.log(loanapplication);
    //    const result = await axios.get(`http://localhost:5000/user/useridExist/${loanapplication.userId}`);
        
     //   console.log(result);
      navigate(`/viewuser/${loanapplication.userId}`);
    };

    const loadApplications= async ()=>{
        const result = await axios.get(`http://localhost:5000/loanapplication/loanapplication/${id}`);
        setLoanapplication(result.data);
        };
        const deleteUser = async (id) => {
            await axios.delete(`http://localhost:5000/user/user/${id}`);
            loadApplications();
        };

        const changeStatusApproved=async()=>{
            loanapplication.status="Approved";
            await axios.put(`http://localhost:5000/loanapplication/update/${id}`, loanapplication);
            navigate(`/viewloanapplication/${id}`);
        };
        const changeStatusRejected=async()=>{
            loanapplication.status="Rejected";
            await axios.put(`http://localhost:5000/loanapplication/update/${id}`, loanapplication);
            navigate(`/viewloanapplication/${id}`);
        };

    return (
        <div className='container'>
            <div className='row'>
                <div className='col-md-6 offset-md-3 border rounded p-4 mt-2 shadow'>
                    <h2 className='fw-semibold text-primary text-center'> Loan Application Details</h2>
                    <div className='card'>
                        <div className='card-header'>
                            <ul className='list-group list-group-flush'>
                                <li className='list-group-item'>
                                    <b>Application Id : </b>
                                    {id}
                                </li>
                                <li className='list-group-item'>
                                    <b>Loan Amount : </b>
                                    {loanapplication.loanAmount}
                                </li>
                                <li className='list-group-item'>
                                    <b>Annual Income : </b>
                                    {loanapplication.annualIncome}
                                </li>
                                <li className='list-group-item'>
                                    <b>Tenure : </b>
                                    {loanapplication.expectedTenure}
                                </li>
                                <li className='list-group-item'>
                                    <b>Loan Used For : </b>
                                    {loanapplication.loanUsedFor}
                                </li>
                                <li className='list-group-item'>
                                    <b>Address : </b>
                                    {loanapplication.address}
                                </li>
                                <li className='list-group-item'>
                                    <b>City : </b>
                                    {loanapplication.city}
                                </li>
                                <li className='list-group-item'>
                                    <b>State : </b>
                                    {loanapplication.state}
                                </li>
                                <li className='list-group-item'>
                                    <b>Status : </b>
                                    {loanapplication.status}
                                </li>
                                <li>
                                <button type="button" onClick={changeStatusApproved} className="btn btn-success">APPROVE</button>
                                </li>
                                <li></li>
                                <li>
                                <button type="button" onClick={changeStatusRejected} className="btn btn-danger">REJECT</button>
                                
                                </li>
                            </ul>
                        </div>
                    </div>
                    <Link className='btn btn-secondary my-4' to={`/adminUserDetails/${loanapplication.userId}`}>Back to User</Link>
                </div>
            </div>
        </div>
    )
}
